<template>
<!-- 当指令中要传字符串时，需要在引号里面套嵌引号 -->
  <div v-theme = "'narrow'" class='wrapper'>
      <input type = 'text' v-model = 'search' placeholder="搜索">
      <div v-for = 'item in filteredBlog' :key='item.id' class = 'block'> 
          <template >
              <router-link :to = "'/blog/'+item.objectId"><h3 v-rainbow>{{item.title|to-uppercase}}</h3></router-link>
              <p>{{item.content|snippet}}</p>
          </template>
      </div>
  </div>
</template>

<script>
export default {
    data(){
        return{
            articles:[],
            search:'',
        }
    },
    created(){
        this.$http.get('https://api2.bmob.cn/1/classes/post-blog',{'headers':{'X-Bmob-Application-Id':"acfefd04e7c2f0e360fe952113009948",
          'X-Bmob-REST-API-Key':'706ddcdbb1232ae3515e0260d2157cbd',
          'Content-Type':'application/json'}}).then(
            res =>{
                this.articles = res.body.results.reverse();
                console.log(this.articles);
            }
        ).catch(
            err=>{
            console.log('err',err);
        })
    },
    computed:{
        filteredBlog(){
            return this.articles.filter(blog=>{
                return blog.title.match(this.search);
            })
        }
    }
}
</script>

<style scoped>
.wrapper{
    margin:20px auto;
}
    .block{
        background-color :#eee;
        width:100%;
        padding:20px;
        margin-top:20px;
        box-sizing: border-box;
    }
    input{
        outline: none;
        box-sizing: border-box;
        width:100%;
        border-radius: 10px;
        height:30px;
        line-height:30px;
        padding:0 10px;
        border:1px solid #ddd;
    }

</style>