<template>
  <div class="add-blog" v-theme = "'narrow'">
    <h2 v-if='!edit'>添加博客</h2>
    <h2 v-else>编辑博客</h2>
    <form v-if="!posted">
      <label>博客标题</label>
      <input type="text" required v-model="blog.title" />
      <br />
      <label>博客内容</label>
      <textarea v-model="blog.content" required></textarea>
      <div class="checkboxes">
        <template v-for="(cate,i) in cates">
          <label :key="i">{{cate}}</label>
          <input type="checkbox" :value="cate" :key="cate" v-model="blog.cate" />
        </template>
      </div>
      <select v-model="blog.author" required>
        <option value='' disabled>请选择</option>
        <option v-for="author in authors" :key="author" :value="author">{{author}}</option>
      </select>
      <br />
      <input type="submit" value="提交" @click.prevent="postBlog" v-if='!edit'/>
      <input type="submit" value="提交修改" @click.prevent="editBlog" v-else/>
    </form>
    <div v-else class = 'tip'>
      <h3 v-if = '!edit'>您的文章发布成功!经过{{countTime}}秒后跳转回首页</h3>
      <h3>您的文章修改成功!经过{{countTime}}秒后跳转回首页</h3>
    </div>
    <hr />
    <div class="preview-blog" v-if="!posted">
      <h3>文章总览</h3>
      <p><b>标题:</b> {{blog.title}}</p>
      <p><b>内容:</b> {{blog.content}}</p>
      <ul><b>标签:</b>
        <li v-for="(cate,i) in blog.cate" :key="i">{{cate}}</li>
      </ul>
      <p><b>作者:</b> {{blog.author}}</p>
    </div>
  </div>
</template>

<script>
import { clearInterval } from 'timers';
export default {
  data() {
    return {
      edit:this.$route.params.id || null,
      posted: false,
      countTime:3,
      blog: {
        title: "",
        content: "",
        cate: [],
        author: ""
      },
      cates: ["vue", "es6", "angular", "webpack"],
      authors: ["sasa", "xiu", "li"]
    };
  },
  created(){
    if(this.edit){
      this.$http.get('https://api2.bmob.cn/1/classes/post-blog/'+this.edit,
        {'headers':{'X-Bmob-Application-Id':"acfefd04e7c2f0e360fe952113009948",
          'X-Bmob-REST-API-Key':'706ddcdbb1232ae3515e0260d2157cbd',
          'Content-Type':'application/json'}}).then(res=>{
            console.log(res);
            this.blog.author= res.body.author;
            this.blog.content = res.body.content;
            this.blog.title = res.body.title;
            this.blog.cate = res.body.cate.split(',');
        }).catch(err=>{
            console.log(err);
        })
    }
  },
  methods: {
    postBlog() {
      this.$http.post("https://api2.bmob.cn/1/classes/post-blog", {
          title: this.blog.title,
          content: this.blog.content,
          cate: this.blog.cate.toString(),
          author: this.blog.author
        },{'headers':{'X-Bmob-Application-Id':"acfefd04e7c2f0e360fe952113009948",
          'X-Bmob-REST-API-Key':'706ddcdbb1232ae3515e0260d2157cbd',
          'Content-Type':'application/json'}})
        .then(res => {
          this.posted = true;
          let timerC = setInterval(()=>{
            if(this.countTime){
              this.countTime--
            }else{
              console.log(timerC);
              window.clearInterval(timerC);
              this.$router.push('/');
            }
          },1000);
        }).catch(err=>{
          console.log('err',err);
        });
    },
    editBlog(){
      this.$http.put('https://api2.bmob.cn/1/classes/post-blog/'+this.edit, {
          title: this.blog.title,
          content: this.blog.content,
          cate: this.blog.cate.toString(),
          author: this.blog.author
        },{'headers':{'X-Bmob-Application-Id':"acfefd04e7c2f0e360fe952113009948",
          'X-Bmob-REST-API-Key':'706ddcdbb1232ae3515e0260d2157cbd',
          'Content-Type':'application/json'}}).then(res=>{
            this.posted = true;
          let timerC = setInterval(()=>{
            if(this.countTime){
              this.countTime--
            }else{
              console.log(timerC);
              window.clearInterval(timerC);
              this.$router.push('/');
            }
          },1000);
      }).catch(err=>{
        console.log('err',err);
      })
    }
  }
};
</script>

<style scoped>
label {
  display: block;
  margin:10px 0;
}
.checkboxes label{
    display: inline-block;
}
input[type="submit"] {
  margin:20px 0;
  outline: none;
        background-color: rgb(199, 44, 44);
        padding:5px 20px;
        margin-right:10px;
        border:none;
        border-radius: 50px;
        color:white;
}
input[type="text"],
select,
textarea {
  width: 100%;
}
textarea{
    height:200px;
}
.preview-blog{
  padding:10px;
  margin:20px 0;
  width:100%;
  border:3px dotted rgb(240, 114, 114);
  box-sizing:border-box;
}
.preview-blog p{
    color:#333;
}
.add-blog{
    margin:20px auto; 
}
.preview-blog b{
  color:#666;
  display:inline-block;
  margin-right:10px;
}
ul{
  text-align: left;
}
.tip h3{
  text-align: center;
  color:#333;
}
</style>