"use strict";

import Vue from 'vue';
import axios from "axios";

// Full config:  https://github.com/axios/axios#request-config
// axios.defaults.baseURL = process.env.baseURL || process.env.apiUrl || '';
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

let config = {
  baseURL: 'https://api2.bmob.cn/1/classes'
  // timeout: 60 * 1000, // Timeout
  // withCredentials: true, // Check cross-site Access-Control
};

const _axios = axios.create(config);
_axios.defaults.headers.common = Object.assign(axios.defaults.headers, {
  'X-Bmob-Application-Id': "acfefd04e7c2f0e360fe952113009948",
  'X-Bmob-REST-API-Key': '706ddcdbb1232ae3515e0260d2157cbd',
  'Content-Type':'application/json',
});
// 拦截器
_axios.interceptors.request.use(
  function (config) {
    // Do something before request is sent前置处理
    return config;
  },
  function (error) {
    // Do something with request error异常处理
    return Promise.reject(error);
  }
);

// Add a response interceptor
_axios.interceptors.response.use(
  function (response) {
    // Do something with response data后置处理
    return response;
  },
  function (error) {
    // Do something with response error异常处理
    return Promise.reject(error);
  }
);

Plugin.install = function (Vue, options) {
  Vue.axios = _axios;
  window.axios = _axios;
  Object.defineProperties(Vue.prototype, {
    axios: {
      get() {
        return _axios;
      }
    },
    $axios: {
      get() {
        return _axios;
      }
    },
  });
};

Vue.use(Plugin)