<template>
  <div id = 'single-blog'>
      <h3>{{blog.title}}</h3>
      <ul>标签：
          <li v-for = '(cate,index) in blog.cate' :key = 'index'>{{cate}}</li>
      </ul>
      <p>{{blog.content}}</p>
      <div class = 'btns'>
          <button @click='deleteBlog()'>删除</button>
          <button @click='editBlog()'>编辑</button>
      </div>
  </div>
</template>

<script>
export default {
    name:'single-blog',
    data(){
        return{
            id:this.$route.params.id,
            blog:{
                title:'',
                cate:[],
                content:'',
                author:''
            }
        }
    },
    methods:{
        deleteBlog(){
            this.$http.delete('https://api2.bmob.cn/1/classes/post-blog/'+this.id,
            {'headers':{'X-Bmob-Application-Id':"acfefd04e7c2f0e360fe952113009948",
          'X-Bmob-REST-API-Key':'706ddcdbb1232ae3515e0260d2157cbd',
          'Content-Type':'application/json'}}).then(res=>{
                console.log(`删除博客${this.id}`);
                this.$router.push('/');
            });
        },
        editBlog(){
            this.$router.push('/edit/'+this.id);
        }
    },
    created:function(){
        this.$http.get('https://api2.bmob.cn/1/classes/post-blog/'+this.id,
        {'headers':{'X-Bmob-Application-Id':"acfefd04e7c2f0e360fe952113009948",
          'X-Bmob-REST-API-Key':'706ddcdbb1232ae3515e0260d2157cbd',
          'Content-Type':'application/json'}}).then(res=>{
            console.log(res);
            this.blog.author= res.body.author;
            this.blog.content = res.body.content;
            this.blog.title = res.body.title;
            this.blog.cate = res.body.cate.split(',');
        }).catch(err=>{
            console.log(err);
        })
    }
}
</script>

<style scoped>
    #single-blog{
        width:400px;
        margin:20px auto;
        padding: 20px;
        background-color:#eee;
    }
    h3{
        color:#333;
    }
    ul{
        color:#999;
        margin:10px 0;
        text-align: left;
    }
    li{
        background-color: bisque;
        border:1px solid rgb(218, 84, 67);
        border-radius: 5px;
        padding:0 2px;
        display:inline-block;
        margin-right:10px;
        color:rgb(218, 84, 67);

    }
    p{
        color:#444;
    }
    .btns{
        margin:20px 0px;
    }
    .btns button{
        outline: none;
        background-color: rgb(199, 44, 44);
        padding:5px 20px;
        margin-right:10px;
        border:none;
        border-radius: 50px;
        color:white;
    }
</style>